@import url('https://fonts.googleapis.com/css2?family=Alkatra&display=swap');
/* @import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap'); */
:root{
    --white:white;
    --text:#cccccc;
    --background:#333333;
    --background0:#222222;
    --black:black;
}
.blue{
    --text:blue;
}
.fa-solid:hover,.fa-brands:hover{
    color: var(--white);
    transform: scale(1.3);
    transition: all .2s;
}
.pink{
    --text:pink;
}
.yellow{
    --text:yellow;
}
::selection{
    background-color: none;
    color: rgb(255, 90, 118);
}
::-webkit-scrollbar {
    float: left;
    width: 10px;
}
::-webkit-scrollbar-track {
  background: var(--text);
}
::-webkit-scrollbar-thumb {
    border: 1px solid;
    border-radius: 10px;
  background: var(--background);
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Alkatra', cursive;
    color: var(--text);
    scroll-behavior: smooth;
}
.dark{
    --white:black;
    --text:#767676;
    --background:white;
    --background0:#f7f7f7;
    --black:white;
}
a,button{
    all: unset;
}
.flex{
    display: flex;
}
.grid{
    display: grid;
}
.grid-center{
    place-items: center;
}
.uppercase{
    text-transform: uppercase;
}
img{
    width: 100%;
    height: 100%;
}
ul{
    list-style-type: none;
}
li{
    margin: 10px 0px;
    /* color: #cccccc; */
}
path{
    color: var(--white);
}
span{
    color: var(--white);
}
h1{
    color: var(--white);

}
a{
    font-size: 16px;
}
a:hover{
    color: var(--white);
}
.nav-link{
    transition: transform .2s;
}
.nav-link:hover{
    transform: scale(1.3);
}
header{
    width: 100%;
    display: grid;
    place-items: center;
}
hr{
    margin: 30px 0px;
    opacity: .5;
}
input,textarea{
    width: 100%;
    padding: 10px;
    margin: 10px 0px;
    border-radius: 5px;
    font-size: 16px;
    border: 1px solid var(--white);
    color: var(--white);
    background-color: transparent;
}

/* Header */
.box{
    background-color: var(--background0);
    padding: 50px;
    margin: 20px 0px;
    border-radius: 100px;
}
.mix{
    padding: 50px;
}
.title-section{
    position: relative;
    padding: 50px 0;
    text-align: center;
}
.title-section h1{
    font-size: 2rem;
    font-weight: 800;
    letter-spacing: 5px;
    text-transform: uppercase;
    margin: 0; 
}
.Not-found{
    width: 400px;
    height: auto;
    text-align: center;
}
.title-bg{
    font-size: 5rem;
    left: 0;
    letter-spacing: 10px;
    line-height: 0;
    position: absolute;
    right: 0;
    top: 50%;
    text-transform: uppercase;
    font-weight: 800;
    opacity: .2;
}



/* Contact */
.location{
    padding: 10px 20px;
    margin: 20px 50px;
    border-radius: 8px;
    text-decoration: none;
    box-shadow: 1px 2px 13px 1px rgba(0, 0, 0, 0.5);
    color: var(--white);
}
.section-title1 {
    font-size: 16px;
    text-align: left;
    letter-spacing: 1px;
    line-height: 0;
    text-transform: uppercase;
    font-weight: 800;
    color: var(--white);
    margin: 10px 0px 20px;
}


.themer{
    width: 300px;
    height: 60px;
    position: absolute;
    left: 5%;
    top: 36%;
    border-radius: 10px;
    box-shadow: 0 0 5px var(--white);

}

.changer svg{
    position: fixed;
    left: 1%;
    top: 30%;
    animation: rotate 2s linear infinite;
}

@keyframes rotate{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
.theme-rap{
    width: 100%;
    height: 100%;
    justify-content: space-around;
    align-items: center;
}
.color-icon{
    width: 20px;
    height: 20px;
    border-radius: 50%;
}

.skull{
    font-size: 5rem;
}
a.active {
    color: var(--white);
    position: relative;
}
a.active::after{
    content: '';
    position: absolute;
    width: 20px;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: var(--white);
}