@media screen and (max-width:1000px){
    .bar{
        display: none;
    }
    section{
        width: 100vw;
    }
    .main-box{
        flex-direction: column;
    }
    .info{
        width: 70vw;
    }
    .menubar{
        display:unset !important;
    }
    .mainchanger{
        display:unset !important;
    }
    footer{
        font-size: 1rem;
    }
}
@media screen and (max-width:720px){
    .card2{
        flex-direction: column;
    }
    .left_card2,.right_card2{
        width: 100%;;
    }
    .left_card2{
        padding: 50px;
    }
}
@media screen and (max-width:480px){
    .title-bg{
        font-size: 4rem;
    }
    .Not-found{
        width: auto;
        padding: 20px;
    }
    .details{
        flex-direction: column;
    }
    .container{
        padding: 20px;
    }
    .box{
        padding: 20px;
        border-radius: 0px;
    }
    .mix{
        padding: 0;
    }
    .location{
        margin: 20px;
    }
    .right_card2{
        margin: 0;
    }
}