.container{
    padding: 50px;
}
.img{
    width: 90%;
    height: 60vh;
    margin: 0 auto 50px auto;
}
.col {
    flex: 0 0 50%;
    max-width: 50%;
}


/* Contact */

.left_card2 {
    width: 50%;
}
.right_card2 {
    width: 50%;
    /* padding: auto 50px; */
    padding: 10px 20px;
    margin: 0px 10px;
    border-radius: 8px;
    text-decoration: none;
    box-shadow: 1px 2px 13px 1px rgba(5, 5, 5, 0.5);
    /* color: var(--changer); */
}
.contact-logo{
    font-size: 1.5rem;
    margin: 10px;
}

.CV{
    width: 200px;
    text-align: center;
    border: 1px solid;
    padding: 10px 30px;
    color: var(--white);
    transition: all 1s;
    text-transform: uppercase;
    letter-spacing: 3px;
}
.CV:hover{
    color: var(--black);
    background-color: var(--white);
}

.box_Knowledges button{
    padding: 10px 20px;
    margin: 10px;
    border-radius: 8px;
    text-decoration: none;
    box-shadow: 1px 2px 13px 1px rgba(0, 0, 0, 0.5);
    color: var(--white);
    display: flex;
    place-content: center;
}

.box_Knowledges button:hover{
    box-shadow: 1px 2px 13px 1px rgba(0, 0, 0, 0.2);
}
.edu{
    border-left: 3px solid ;
    border-radius: 10px;
    transition: all .2s;
}
.edu:nth-child(odd){
    border-left: 0px;
    border-right: 3px solid ;
    text-align: end;
}
.edu:hover{
    box-shadow: 0px 0px 3px var(--white);
}