.protfolio-flex{
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
}

.Child{
    width: 350px;
    height: 200px;
    border-radius: 10px;
    overflow: hidden;
    transition: all .5s;
    background-color: white;
    position: relative;
}
.Child:hover{
    transform: scale(1.1);
}
.Child .childbox{
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: 0;
    left: 0;
    background: linear-gradient(rgba(4, 0, 255, 0.1),rgba(96, 255, 96, 0.1),rgba(149, 149, 255, 0.1));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    place-items: center;
    transform: translateY(100%);
    transition: all .2s;
}
.Child:hover .childbox{
    transform: translateY(0%);
}
.portfolio-link{
    font-size: 3rem;
}