.conatiner{
    height: 100vh;
}
.bar{
    width: 25vw;
    height: 100vh;
    background-color: var(--background0);
    position: sticky;
    top: 0;
    align-items: center;
}
.mode{
    position:absolute;
    top:50px;
    right:50px;
}
.mode-icon{
    font-size: 1.5rem;
}
.navbar_box{
    width: 280px;
    height: 90vh;
    margin: 40px -20px 0 ;
    position: absolute;
    left: 0;
    top: 0;
    background-color: var(--background0);
    border-radius: 20px;
    z-index: 99;
    display: grid;
    place-items: center;
}
.navbar-link{
    color: var(--color);
}
