section{
    background-color: var(--background);
    width: 75vw;
    height: 100vh;
    display: grid;
    overflow-y: auto;
}
.title-name{
    font-size: 3rem;
}
body{
    position: fixed;
}
.profiler-img{
    border-radius: 50%;
    overflow: hidden;
    position: relative;
}
.bar{
    flex-direction: column;
    justify-content: space-evenly;
}
